<template>
  <div id="prict_box">
    <div class="padd-25">说两句？</div>
    <div>
      <el-form @submit.native.prevent action="">
        <el-input
          type="textarea"
          autosize
          :rows="2"
          resize='none'
          placeholder="请输入内容"
          v-model="textarea"
          max='10'
          style="width: 300px;">
        </el-input>
      </el-form>
      <div>
        <button @click='clickButton' class="Mybtn mg-bt-10">发送</button>
      </div>
    </div>
    <div class="txal mg-b-30">
      <div class="padd-20 dslb mg-b-5" v-for='item,index of dataList' :key="item._id" style="width: 750px;border-bottom: 1px solid #eeeeee;">
        <div class="fsb mg-b-10">
          <div>{{item.name}}</div>
          <div class="f14 gray-2">{{item.createTime | date}}</div>
        </div>
        <div class="txalS mg-l-6">{{item.value}}</div>
      </div>
      <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[10, 20, 30, 40]"
        :page-size="limit"
        hide-on-single-page
        layout="total, sizes, prev, pager, next, jumper"
        :total="listTotal">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        textarea:'',
        dataList:[],
        currentPage:1,
        listTotal:0,
        limit:10,
        offset:1
      };
    },
    computed: {

    },
    created(){
      this.getData()
    },
    mounted() {

    },
    updated(){

    },

    methods: {
      clickButton(){
        console.log(this.textarea)
        if(this.textarea==""){
          this.$message({ message: '内容不能为空', duration: 1500,iconClass:'none' })
          return false
        }
        if(!this.$store.state.user._id){
          this.$message({ message: '请先登录', duration: 1500,iconClass:'none' })
          return false
        }
        let data={value:this.textarea,userid:this.$store.state.user._id,name:this.$store.state.user.name}
        this.$api.addproposal(data,res=>{
          if(res.code==1000){
            this.$store.commit('updateUser',res.data)
            this.textarea='',this.getData();
          }
        })
      },
      handleSizeChange(val) {
        this.limit=val
        this.getData()
      },
      handleCurrentChange(val) {
        this.offset=val
        this.getData()
      },
      getData(){
        let data={
          _id:this.$store.state.user._id,
          limit:this.limit,
          offset:this.offset
        }
        this.$api.haveproposal(data,res=>{
          console.log(res)
          this.dataList=res.data.list
          this.listTotal=res.data.total
        })
      }
    }
  }
</script>


<style lang="less" scoped>
  #prict_box{
    color: aliceblue;
    text-align: center;
  }
  .socketBox{
    height: 600px;
    width: 350px;

    /* border: 2px solid; */
  }
  .contentBox{
    overflow-y: auto;
    height: 517px;
    /* margin-top: 10px; */
  }
  .contentBox::-webkit-scrollbar{
    display: none;
  }
  .back_{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-image: linear-gradient( #ccec1c,#ad7d7d00,);
    opacity: .3;
    z-index: -1;
  }
</style>



